*{ margin: 0; padding: 0; -webkit-box-sizing: border-box; box-sizing: border-box; list-style-type: none; }

body {
  font-family: 'Muli', Helvetica, Arial;
  font-size: 16px;
}

.app {
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.main-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 80px;
  padding: 2em 5em;
  margin: 0;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: transparent;
  -webkit-box-shadow: 0 6px 15px rgba(36,37,38,0.08);
          box-shadow: 0 6px 15px rgba(36,37,38,0.08);
}

.main-header h2 > a {
  font-family: 'Lobster', cursive;
  background-image: -webkit-linear-gradient(330deg, #680ECF 0%,  #FF4376 100%);
  background-image: -o-linear-gradient(330deg, #680ECF 0%,  #FF4376 100%);
  background-image: linear-gradient(120deg, #680ECF 0%,  #FF4376 100%);
  color:transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.main-header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-filter: blur(40px);
          filter: blur(40px);
  z-index: -1;
}

.color-list {
  width: 80%;
  height: 100%;
  margin: 8em auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.color-list__item {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  width: 160px;
  height: 180px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #fff;
  -webkit-box-shadow: 0 6px 15px rgba(36,37,38,0.08);
          box-shadow: 0 6px 15px rgba(36,37,38,0.08);
  border-collapse: collapse;
  border-radius: 16px;
  cursor: pointer;
  margin: 1.5em;
  -webkit-transition: -webkit-box-shadow 0.25s ease, -webkit-transform 0.25s ease;
  transition: -webkit-box-shadow 0.25s ease, -webkit-transform 0.25s ease;
  -o-transition: box-shadow 0.25s ease, transform 0.25s ease;
  transition: box-shadow 0.25s ease, transform 0.25s ease;
  transition: box-shadow 0.25s ease, transform 0.25s ease, -webkit-box-shadow 0.25s ease, -webkit-transform 0.25s ease;
}

.color-list__item:not(:last-of-type) {
  margin-right: 1.5em;
}

.color-display {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
  border-radius: 16px 16px 0 0;
}

.color-list__item:hover {
  -webkit-box-shadow: 0 6px 15px rgba(36,37,38, 0);
          box-shadow: 0 6px 15px rgba(36,37,38, 0);
}

.color-list.copied:after {
  content: 'Copied!';
  font-family: 'Muli';
  width: 100%;
  text-align: center;
  padding: 1em 0;
  font-size: 5rem;
  font-weight: bold;
  color: #333;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
          animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

.color-data {
  padding: 1em;
  font-size: .90rem;
}

.color {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.color:not(:last-of-type) {
  margin-bottom: .50em;
}

.color-box {
  width: 10px;
  height: 10px;
  margin-right: 1em;
  border-radius: 50%;
}

.donate {
  padding-bottom: .20em;
  border-bottom: 3px solid sandybrown;
}

.donate a {
  font-size: .80rem;
  font-weight: bold;
  color: #333;
  text-decoration: none;
}

footer {
  margin: 5em 0;
  font-size: .80rem;
  color: #333;
}

footer .fa-heart {
  font-size: .70rem;
}

footer a {
  text-decoration: none;
  color: #39dbfa;
}


/**___ Keyframes ___**/
@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.2em;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.2em;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


/**____ Media Queries ____**/
@media (max-width: 600px) {
  .color-list__item {
    width: 100%;
    height: 300px;
    margin: 2em 0;
  }

  .main-header {
    padding: 2em 1em;
  }

  .main-header h2, .main-header a {
    font-size: 1.5rem;
  }

  .donate a {
    font-size: .75rem;
  }
}


